/* Content.css */
.content-container {
  /* Mant√©m o scroll snapping para facilitar a navega√ß√£o */
  scroll-snap-type: y mandatory;
  overflow-y: scroll;
}

/* Cada se√ß√£o ocupa pelo menos uma tela inteira e centraliza o conte√∫do */
.section {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 20px;
  scroll-snap-align: start;
}

/* üîπ Vers√£o Responsiva para Celulares */
@media (max-width: 768px) {
  .section {
    padding: 15px;
  }

  .content-container {
    scroll-snap-type: none; /* Remove o efeito de snap em celulares para evitar bugs de rolagem */
    overflow-y: auto;
  }
}
