@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@400;700&display=swap');

.contato-container {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 80px;
  padding: 40px;
  color: #e0e0e0;
  text-align: center;
}

/* Coluna da esquerda */
.contato-left {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: -50px;
  gap: 20px;
}

.contato-left h2 {
  font-family: 'Montserrat', sans-serif;
  font-size: 2.5rem;
  color: #00bcd4;
}

.icon-links {
  display: flex;
  gap: 20px;
}

/* √çcones */
.contact-icon {
  font-size: 2.5rem;
  color: #fff;
  transition: transform 0.2s ease-in-out, color 0.2s;
}

.contact-icon:hover {
  transform: scale(1.2);
}

.whatsapp-icon:hover {
  color: #25D366;
}

.email-icon:hover {
  color: #D44638;
}

.linkedin-icon:hover {
  color: #0077B5;
}

/* Linha divis√≥ria */
.divider {
  width: 2px;
  height: 100px;
  background-color: #bbb;
}

/* Coluna da direita */
.contato-right {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px;
}

.contato-right p {
  font-family: 'Montserrat', sans-serif;
  font-size: 1.2rem;
}

.contato-right a {
  color: #00bcd4;
  text-decoration: none;
}

.contato-right a:hover {
  text-decoration: underline;
}

/* Bot√£o de download do CV */
.download-cv-button {
  background-color: #292929;
  color: white;
  border: none;
  padding: 12px 20px;
  border-radius: 5px;
  cursor: pointer;
  font-family: 'Montserrat', sans-serif;
  font-size: 1rem;
  transition: background-color 0.3s;
  margin-top: 10px;
}

.download-cv-button:hover {
  background-color: #0097a7;
}

/* üîπ Vers√£o Responsiva para Celulares */
@media (max-width: 768px) {
  .contato-container {
    flex-direction: column;
    gap: 30px;
    padding: 20px;
  }

  .contato-left h2 {
    font-size: 2rem;
  }

  .contact-icon {
    font-size: 2rem;
  }

  .divider {
    display: none;
  }

  .contato-right p {
    font-size: 1rem;
  }

  .download-cv-button {
    font-size: 0.9rem;
    padding: 10px 18px;
  }
}
