@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@400;700&family=Roboto+Mono&display=swap');

.sobre-container {
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: flex-start; /* Alinhado √† esquerda, estilo "linha de c√≥digo" */
  justify-content: center;
  text-align: left;
  padding: 20px;
  gap: 20px;
}

/* Nome */
.name {
  font-family: 'Montserrat', sans-serif;
  font-size: 4rem;
  margin: 0;
  line-height: 1;
}

/* Exibe as partes do nome como blocos sem margem extra */
.name span {
  display: block;
  margin: 0;
}

.name-gustavo {
  animation: slideInLeft 1s ease-out forwards;
}

.name-fernandez {
  animation: slideInRight 1s ease-out forwards;
}

/* Cargo */
.role {
  font-family: 'Montserrat', sans-serif;
  font-size: 2.5rem;
  color: #00bcd4;
  position: relative;
  overflow: hidden;
  margin: 0;
}

/* Pseudo-elemento para simular o efeito de revela√ß√£o de tinta guache de baixo para cima */
.role::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  height: 100%;
  background: #121212;
  filter: blur(2px);
  animation: revealOverlay 2s ease-out forwards;
}

/* Descri√ß√£o */
.description {
  font-family: 'Roboto Mono', monospace;
  font-size: 1.2rem;
  max-width: 600px;
  line-height: 1.5;
  margin: 0;
}

/* Anima√ß√µes */
@keyframes slideInLeft {
  from {
    transform: translateX(-100%);
    opacity: 0;
  }
  to {
    transform: translateX(0);
    opacity: 1;
  }
}

@keyframes slideInRight {
  from {
    transform: translateX(100%);
    opacity: 0;
  }
  to {
    transform: translateX(0);
    opacity: 1;
  }
}

/* Efeito de revela√ß√£o de baixo para cima */
@keyframes revealOverlay {
  from {
    transform: translateY(0);
  }
  to {
    transform: translateY(-100%);
  }
}

/* üîπ Vers√£o Responsiva para Celulares */
@media (max-width: 768px) {
  .sobre-container {
    align-items: center; /* Centraliza tudo no celular */
    text-align: center;
    padding: 15px;
  }

  .name {
    font-size: 3rem;
  }

  .role {
    font-size: 2rem;
  }

  .description {
    font-size: 1rem;
    max-width: 90%;
  }
}
