/* Navbar */
.navbar {
  position: fixed;
  top: 50%;
  left: 20px;
  transform: translateY(-50%);
  background-color: #1f1f1f;
  padding: 20px;
  border-radius: 12px;
  display: flex;
  flex-direction: column;
  gap: 10px;
  z-index: 1000;
}

/* Bot√µes da Navbar */
.nav-button {
  background: none;
  border: none;
  color: #e0e0e0;
  font-size: 16px;
  cursor: pointer;
  padding: 10px;
  transition: background-color 0.3s, color 0.3s;
  display: flex;
  align-items: center;
  border-radius: 8px;
}

.nav-button.active,
.nav-button:hover {
  color: #00bcd4;
  background-color: rgba(0, 188, 212, 0.1);
}

.nav-button .icon {
  margin-right: 8px;
}

/* üîπ Vers√£o Responsiva para Celulares */
@media (max-width: 768px) {
  .navbar {
    display: none;
  }


}
