@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@400;700&family=Roboto+Mono:wght@400;700&display=swap');

.trabalhos-container {
  padding: 40px;
  text-align: center;
  color: #e0e0e0;
  margin-top: -190px;
}

.trabalhos-container h2 {
  font-family: 'Montserrat', sans-serif;
  font-size: 2.5rem;
  color: #00bcd4;
  margin-bottom: 40px;
}

/* Grid com 3 colunas fixas */
.trabalhos-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 30px;
  justify-items: center;
  align-items: center;
  opacity: 0;
  transform: translateY(30px);
  transition: opacity 0.8s ease-out, transform 0.8s ease-out;
}

.trabalhos-grid.animate {
  opacity: 1;
  transform: translateY(0);
}

/* Estiliza√ß√£o dos blocos */
.trabalho-block {
  background-color: #1e1e1e;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 2px 2px 10px rgba(0, 0, 0, 0.3);
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  width: 390px;
  height: 450px;
  gap: 15px;
  font-family: 'Roboto Mono', monospace;
}

/* Ajusta as imagens dentro dos blocos */
.trabalho-img {
  width: 100%;
  height: 160px;
  border-radius: 8px;
  object-fit: cover;
  border: 2px solid #888;
}

/* Links nos t√≠tulos */
.trabalho-block h3 a {
  text-decoration: none;
  color: inherit;
}

.trabalho-block h3 a:hover {
  text-decoration: underline;
}

/* Cor do t√≠tulo "AOC (Alot of Cash)" */
.aoc-title {
  color: #4CAF50;
}

/* Cor do t√≠tulo "Simulador de Produ√ß√£o de Energia Solar" */
.solar-title {
  color: #FFD700;
}

/* Bot√£o de visitar site */
.visit-button {
  background-color: #00bcd4;
  color: #121212;
  border: none;
  padding: 10px 20px;
  border-radius: 5px;
  cursor: pointer;
  font-family: 'Montserrat', sans-serif;
  font-size: 1rem;
  transition: background-color 0.3s;
}

.visit-button:hover {
  background-color: #0097a7;
}

/* Bloco exclusivo para o GitHub */
.github-block {
  justify-content: center;
  height: 450px;
  background-color: #2c2c2c;
}

/* √çcone do GitHub maior */
.github-icon {
  font-size: 4.5rem;
  color: #fff;
}

/* Bot√£o para ver mais projetos */
.github-button {
  background-color: #00bcd4;
  color: #121212;
  border: none;
  padding: 10px 20px;
  border-radius: 5px;
  cursor: pointer;
  font-family: 'Montserrat', sans-serif;
  font-size: 1rem;
  transition: background-color 0.3s;
}

.github-button:hover {
  background-color: #0097a7;
}

/* üîπ Vers√£o Responsiva para Celulares */
@media (max-width: 768px) {
  .trabalhos-container {
    margin-top: 0;
    padding: 20px;
  }

  .trabalhos-container h2 {
    font-size: 2rem;
    margin-bottom: 30px;
  }

  .trabalhos-grid {
    grid-template-columns: 1fr; /* Transforma os blocos em uma √∫nica coluna */
    gap: 20px;
  }

  .trabalho-block {
    width: 90%;
    height: auto;
    padding: 15px;
  }

  .trabalho-img {
    height: 140px;
  }

  .github-block {
    height: auto;
  }

  .github-icon {
    font-size: 3.5rem;
  }

  .visit-button,
  .github-button {
    font-size: 0.9rem;
    padding: 8px 16px;
  }
}
