@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@400;700&family=Roboto+Mono:wght@400;700&display=swap');

.resumo-container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  gap: 80px; /* Espa√ßo entre a coluna esquerda e a coluna direita */
  padding: 40px;
}

/* Centraliza o conte√∫do de cada coluna */
.left-column {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  flex: 1;
  min-width: 400px;
  gap: 40px; /* Espa√ßamento entre Forma√ß√£o e Cursos */
  margin-top: 75px;
}

.right-column {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  flex: 1;
  min-width: 400px;
}

/* Blocos para Forma√ß√£o e Cursos */
.block {
  background-color: #1e1e1e;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 2px 2px 10px rgba(0, 0, 0, 0.3);
  width: 100%;
  text-align: center;
}

/* Garante que os blocos tenham o mesmo tamanho */
.same-height {
  height: 140px;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

/* Estiliza√ß√£o dos T√≠tulos */
.block h3,
.tech-title {
  color: #00bcd4;
  font-family: 'Montserrat', sans-serif;
  font-size: 1.8rem;
  margin-bottom: 15px;
}

/* Texto dentro dos blocos */
.block p {
  font-family: 'Roboto Mono', monospace;
  font-size: 1.3rem;
}

/* Grade de Tecnologias 3x3 */
.tech-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr); /* 3 colunas fixas */
  gap: 20px;
  justify-items: center;
  align-items: center;
}

/* Todos os quadrados ter√£o o mesmo tamanho */
.tech-item {
  width: 120px;
  height: 120px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: #2c2c2c;
  border-radius: 8px;
  text-align: center;
  box-shadow: 2px 2px 10px rgba(0, 0, 0, 0.3);
  opacity: 0;
  transform: translateY(20px);
}

/* Aplica a anima√ß√£o apenas quando a classe 'animate' est√° ativa */
.tech-grid.animate .tech-item {
  animation: fadeInUp 0.6s ease-out forwards;
}

/* Delays aleat√≥rios para os itens */
.tech-grid.animate .tech-delay-1 { animation-delay: 0.3s; }
.tech-grid.animate .tech-delay-2 { animation-delay: 0.5s; }
.tech-grid.animate .tech-delay-3 { animation-delay: 0.2s; }
.tech-grid.animate .tech-delay-4 { animation-delay: 0.8s; }
.tech-grid.animate .tech-delay-5 { animation-delay: 0.6s; }
.tech-grid.animate .tech-delay-6 { animation-delay: 1s; }
.tech-grid.animate .tech-delay-7 { animation-delay: 0.4s; }
.tech-grid.animate .tech-delay-8 { animation-delay: 1.2s; }
.tech-grid.animate .tech-delay-9 { animation-delay: 0.7s; }

.tech-item:hover {
  transform: scale(1.1);
}

/* Estilo dos √≠cones */
.tech-icon {
  font-size: 2.5rem;
  color: #fff;
  filter: grayscale(100%);
}

/* Texto dos √≠cones */
.tech-item span {
  font-family: 'Roboto Mono', monospace;
  font-size: 1.2rem;
  color: #e0e0e0;
  margin-top: 8px;
}

/* Anima√ß√£o fadeInUp */
@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* üîπ Vers√£o Responsiva para Celulares */
@media (max-width: 768px) {
  .resumo-container {
    flex-direction: column;
    gap: 40px;
    padding: 20px;
  }

  .left-column,
  .right-column {
    min-width: 100%;
    text-align: center;
  }

  .tech-grid {
    grid-template-columns: repeat(2, 1fr); /* Muda para 2 colunas no celular */
  }

  .block {
    padding: 15px;
  }

  .block h3,
  .tech-title {
    font-size: 1.5rem;
  }

  .block p {
    font-size: 1.1rem;
  }

  .tech-item {
    width: 100px;
    height: 100px;
  }

  .tech-icon {
    font-size: 2rem;
  }

  .tech-item span {
    font-size: 1rem;
  }
}
